<template>
  <aside class="map border">
    <div id='container'></div>
  </aside>
</template>

<script>
import AMapLoader from '@amap/amap-jsapi-loader';
import {mapMutations} from "vuex";

export default {
  name: "Map",
  methods: {
	  ...mapMutations(['CHANGE']),
    initMap() {
      AMapLoader.load({
        key: "0f680fc17ef03445c8e5548cc28a7225",             // Áî≥ËØ∑Â•ΩÁöÑWebÁ´ØÂºÄÂèëËÄÖKeyÔºåÈ¶ñÊ¨°Ë∞ÉÁî® load Êó∂ÂøÖÂ°´
        version: "2.0",      // ÊåáÂÆöË¶ÅÂä†ËΩΩÁöÑ JSAPI ÁöÑÁâàÊú¨ÔºåÁº∫ÁúÅÊó∂ÈªòËÆ§‰∏∫ 1.4.15
        plugins: ['AMap.Geolocation'],       // ÈúÄË¶Å‰ΩøÁî®ÁöÑÁöÑÊèí‰ª∂ÂàóË°®ÔºåÂ¶ÇÊØî‰æãÂ∞∫'AMap.Scale'Á≠â
      }).then((AMap) => {
        // this.map = new AMap.Map("container", {  //ËÆæÁΩÆÂú∞ÂõæÂÆπÂô®id
        //   viewMode: "3D",    //ÊòØÂê¶‰∏∫3DÂú∞ÂõæÊ®°Âºè
        //   zoom: 15,           //ÂàùÂßãÂåñÂú∞ÂõæÁ∫ßÂà´
        //   center: [120, 28], //ÂàùÂßãÂåñÂú∞Âõæ‰∏≠ÂøÉÁÇπ‰ΩçÁΩÆ
        // });

        // ÂàõÂª∫Â∞èÁÇπÊ†áËÆ∞
        // let marker = new AMap.Marker({
        //   position: [120.699242, 27.99375] // Âü∫ÁÇπÂùêÊ†á
        // });
        // // Âú∞ÂõæÊ∑ªÂä†Ê†áËÆ∞
        // this.map.add(marker);

        let map = new AMap.Map('container', {
          resizeEnable: true
        })
        AMap.plugin('AMap.Geolocation',  ()=> {
          let geolocation = new AMap.Geolocation({
            enableHighAccuracy: true,// È´òÁ≤æÂ∫¶ÂÆö‰Ωç
            timeout: 10000,          // Ë∂ÖËøá10ÁßíÂêéÂÅúÊ≠¢ÂÆö‰Ωç
            buttonPosition: 'RB',    // ÊåâÈíÆÂÅúÈù†‰ΩçÁΩÆ
            buttonOffset: new AMap.Pixel(10, 20),
            zoomToAccuracy: true,    // ÂÆö‰ΩçÊàêÂäüÂêéË∞ÉÊï¥Âú∞ÂõæËßÜÈáé
          })
          map.addControl(geolocation)
          geolocation.getCurrentPosition( (status, geoResult)=> {
            // ÈÄöËøá computed Â∞Ü state ‰∏≠Ë¶ÅÁõëËßÜÁöÑÊï∞ÊçÆÂèñÂá∫Êù•ÔºåÁÑ∂ÂêéÈÄöËøá watchÁõëËßÜÂ±ûÊÄßÁõëËßÜÂÖ∂ÂèòÂåñ
            this.$store.commit('CHANGE',[geoResult.position.lng,geoResult.position.lat])
          })
        })
      }).catch(e => {
        console.log(e);
      })
    },
  },
  //DOMÂàùÂßãÂåñÂÆåÊàêËøõË°åÂú∞ÂõæÂàùÂßãÂåñ
  mounted() {
    this.initMap();

  },
}
</script>

<style lang="less">
.map {
  width: 40%;
}

#container {
  height: 100%;

  a, div.amap-copyright {
    display: none !important;
  }
}

</style>
